import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import JournalCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/generalLedger/journal/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";


const TabPane = Tabs.TabPane;

const redirectTo = "/dashboard/general-ledgers/journals"

class CreateJournal extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createJournal, getImportJournals } = this.props;
    createJournal();
    getImportJournals();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.journal.error) {
      notification("error", this.props.journal.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.journal.success) {
      this.props.history.replace(redirectTo + "/update/" + this.props.journal.id_journal)
    }
  }

  handleSubmit = value => {
    const { storeJournal } = this.props;
    storeJournal(value);
  };

  render() {
    const { user } = this.props.auth;
    let { permissions, default_employee_location } = user ? user : [];
    permissions = permissions ? permissions : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];
    // console.log(permissions);

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="journals.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="journals.tab.detail" />}
              key="1"
            >
              <JournalCreateForm
                submit={this.handleSubmit}
                journal={this.props.journal}
                permissions={permissions}
                default_employee_location={default_employee_location}
              />
            </TabPane>
            <TabPane tab={<IntlMessages id="journals.tab.attachment" />}
              key="2"
              disabled
            >
            </TabPane>
          </Tabs>

        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    journal: state.journal,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateJournal);
