import React, { Component } from "react";
import { Form, InputNumber, Select } from "antd";
import EditableContext from "./editableContext";
import IntlMessages from "../../../../components/utility/intlMessages";
import NumberFormat from "../../../App/NumberFormat";

const FormItem = Form.Item;
const Option = Select.Option;

class EditableCell extends Component {
  state = {
    editing: false
  };

  toggleEdit = () => {
    const editing = !this.state.editing;

    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
      }
    });
  };

  handleClickOutside = e => {
    const { editing } = this.state;

    if (editing && !this.cell) { this.toggleEdit(); } else if (editing && this.cell !== e.target && !this.cell.contains(e.target)) {
      this.save();
      document.removeEventListener("click", this.handleClickOutside, true);
    }
  };

  save = (name, id) => {
    const { record, handleSave,
      // currencies, 
      locations } = this.props;

    this.form.validateFields((error, values) => {
      if (error) {
        return;
      }

      this.toggleEdit();

      // if (name && name === "currency_code") {
      //   const [currency] = currencies.filter(
      //     currData => currData.id_currency === id
      //   );
      //   const newRecord = Object.assign({}, record, {
      //     id_currency: id,
      //     currency_code: currency.currency_code
      //   });
      //   handleSave(newRecord);

      // }
      if (name && name === "location_name") {
        const [location] = locations.filter(
          currData => currData.id_location === id
        );
        const newRecord = Object.assign({}, record, {
          id_location: id,
          location_name: location.location_name
        });
        handleSave(newRecord);
      } else {
        // let newValues = {
        //   ...values
        // }
        // console.log(values.credit_amount);
        if (values.debit_amount) {
          // console.log("masuk d");
          // newValues = {
          //   ...values,
          //   credit_amount:0
          // }
          values.credit_amount = 0;
        } else if (values.credit_amount) {
          // console.log("masuk c");
          // newValues = {
          //   ...values,
          //   debit_amount:0
          // }
          values.debit_amount = 0;
        }
        // console.log(values);
        handleSave({ ...record, ...values });
      }
    });
  };

  render() {
    const { editing } = this.state;
    const {
      editable,
      dataIndex,
      title,
      record,
      index,
      handleSave,
      currencies,
      locations,
      status,
      is_base,
      ...restProps
    } = this.props;
    //console.log(is_base, status, editing, dataIndex);
    return (
      <td ref={node => (this.cell = node)} {...restProps}>
        {editable ? (
          <EditableContext.Consumer>
            {form => {
              this.form = form;
              // if(dataIndex === "currency_code"){

              //   return (
              //     <FormItem style={{ margin: 0 }}>
              //       {form.getFieldDecorator(dataIndex, {
              //         initialValue: record.id_currency,
              //         // rules: [
              //         //   {
              //         //     required: true,
              //         //     message: <IntlMessages id="required" />
              //         //   }
              //         // ],
              //       })(
              //         <Select
              //           ref={node => (this.input = node)}
              //           //allowClear
              //           showSearch
              //           style={{ width: "100%" }}
              //           onSelect={this.save.bind(this, "currency_code")}

              //         >
              //           {currencies.map((data, key) => (
              //             <Option key={data.id_currency} value={data.id_currency}>
              //               {data.currency_code}
              //             </Option>
              //           ))}
              //         </Select>
              //       )}
              //     </FormItem>
              //   );
              // }
              if (editing && dataIndex === "location_name") {
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      initialValue: record.id_location
                      // rules: [
                      //   {
                      //     required: true,
                      //     message: <IntlMessages id="required" />
                      //   }
                      // ],
                    })(
                      <Select
                        ref={node => (this.input = node)}
                        showSearch
                        style={{ width: "100%" }}
                        onSelect={this.save.bind(this, "location_name")}
                      >
                        {locations.map((data, key) => (
                          <Option
                            key={data.id_location}
                            value={data.id_location}
                          >
                            {data.location_name}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </FormItem>
                );
              } else if (
                editing &&
                dataIndex === "debit_amount" &&
                status === 1
              ) {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                      initialValue: record[dataIndex]
                    })(
                      <InputNumber
                        ref={node => (this.input = node)}
                        style={{ width: "100%" }}
                        onPressEnter={this.save}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        min={0}
                        // precision={2}
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      />
                    )}
                  </FormItem>
                );
              } else if (
                !editing &&
                dataIndex === "debit_amount" &&
                status === 1
              ) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {record[dataIndex]
                      ? <NumberFormat value={parseFloat(record[dataIndex])} />
                      : "0"}
                  </div>
                );
              } else if (
                !editing &&
                dataIndex === "debit_amount" &&
                status !== 1
              ) {
                return record[dataIndex]
                  ? <NumberFormat value={parseFloat(record[dataIndex])} />
                  : "0"
              } else if (
                editing &&
                dataIndex === "credit_amount" &&
                status === 1
              ) {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                      initialValue: record[dataIndex]
                    })(
                      <InputNumber
                        ref={node => (this.input = node)}
                        style={{ width: "100%" }}
                        onPressEnter={this.save}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        min={0}
                        // precision={2}
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      />
                    )}
                  </FormItem>
                );
              } else if (
                !editing &&
                dataIndex === "credit_amount" &&
                status === 1
              ) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {record[dataIndex]
                      ? <NumberFormat value={parseFloat(record[dataIndex])} />
                      : "0"}
                  </div>
                );
              } else if (
                !editing &&
                dataIndex === "credit_amount" &&
                status !== 1
              ) {
                return record[dataIndex]
                  ? <NumberFormat value={parseFloat(record[dataIndex])} />
                  : "0"
              }
              else if (
                editing &&
                dataIndex === "currency_rate" &&
                status === 1
              ) {
                document.addEventListener(
                  "click",
                  this.handleClickOutside,
                  true
                );
                return (
                  <FormItem style={{ margin: 0 }}>
                    {form.getFieldDecorator(dataIndex, {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ],
                      initialValue: record[dataIndex]
                    })(
                      <InputNumber
                        ref={node => (this.input = node)}
                        style={{ width: "100%" }}
                        onPressEnter={this.save}
                        formatter={value =>
                          `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={value => value.replace(/\$\s?|(,*)/g, "")}
                        min={0.01}
                      // precision={2}
                      />
                    )}
                  </FormItem>
                );
              }
              else if (
                !editing &&
                dataIndex === "currency_rate" &&
                status === 1 &&
                record['is_base'] === 0
              ) {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {record[dataIndex]
                      ? <NumberFormat value={parseFloat(record[dataIndex])} />
                      : "0"}
                  </div>
                );
              }
              else if (
                !editing &&
                dataIndex === "currency_rate" &&
                status === 1 &&
                record['is_base'] === 1
              ) {
                return record[dataIndex]
                  ? <NumberFormat value={parseFloat(record[dataIndex])} />
                  : "0"
              }
              else if (
                !editing &&
                dataIndex === "currency_rate" &&
                status !== 1
              ) {
                return record[dataIndex]
                  ? <NumberFormat value={parseFloat(record[dataIndex])} />
                  : "0"
              }
              else if (status !== 1) {
                return restProps.children;
              } else {
                return (
                  <div
                    className="editable-cell-value-wrap"
                    onClick={this.toggleEdit}
                  >
                    {restProps.children}
                  </div>
                );
              }
            }}
          </EditableContext.Consumer>
        ) : (
            restProps.children
          )}
      </td>
    );
  }
}

export default EditableCell;
