const actions = {
  getJournals: (page, search, filter) => ({
    type: 'GET_JOURNALS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getImportJournals: (page, search, filter) => ({
    type: 'GET_IMPORT_JOURNALS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createJournal: () => ({
    type: 'CREATE_JOURNAL_REQUEST'
  }),
  storeJournal: data => ({
    type: 'STORE_JOURNAL_REQUEST',
    payload: { body: data }
  }),
  editJournal: id_journal => ({
    type: 'EDIT_JOURNAL_REQUEST',
    payload: { id_journal: id_journal }
  }),
  updateJournal: (id_journal, data) => ({
    type: 'UPDATE_JOURNAL_REQUEST',
    payload: {
      id_journal: id_journal,
      body: data
    }
  }),
  showJournal: id_journal => ({
    type: 'SHOW_JOURNAL_REQUEST',
    payload: { id_journal: id_journal }
  }),
  processJournal: id_journal => ({
    type: "PROCESS_JOURNAL_REQUEST",
    payload: { id_journal }
  }),
  cancelJournal: (id_journal, data) => ({
    type: "CANCEL_JOURNAL_REQUEST",
    payload: { 
      id_journal,
      body : data
    }
  }),
  uploadDocument: (id_journal, data) => ({
    type: "UPLOAD_JOURNAL_DOCUMENT_REQUEST",
    payload: {
      id_journal,
      body : data
    }
  }),
  downloadDocument: id_journal_document => ({
    type: "DOWNLOAD_JOURNAL_DOCUMENT_REQUEST",
    payload: {id_journal_document}
  }),
  deleteDocument: id_journal_document => ({
    type: "DELETE_JOURNAL_DOCUMENT_REQUEST",
    payload: { id_journal_document }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
