import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form, Table } from "antd";
import EditableCell from "./editableCell";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import { getCurrencyRate } from "../../../../helpers/helper";
import NumberFormat from "../../../App/NumberFormat";
import CardWrapper from "../../../App/app.style";
import ModalDetail from "./modalDetail";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

const getTotal = arr => {
  let total_debit_amount = 0;
  let total_credit_amount = 0;
  arr.map(data => {
    total_debit_amount += parseFloat(data.debit_amount) * parseFloat(data.currency_rate);
    total_credit_amount += parseFloat(data.credit_amount) * parseFloat(data.currency_rate);
  });

  return {
    total_debit_amount,
    total_credit_amount
  };
};

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { journal_details } = props;

    this.state = {
      dataSource: [...journal_details],
      count: journal_details.length + 1,
      total_debit_amount: 0,
      total_credit_amount: 0,
      visible: false,
      selectedItem: {}
    };


  }

  componentDidMount() {
    const {
      temp_journal_detail,
      temp_journal_details,
      clearJournalDetail,
      clearJournalDetails,
      id_location,
      locations,
      accounts,
      transaction_date,
      currencies,
    } = this.props;

    const [location] = locations.filter(
      currData => currData.id_location === id_location
    );

    // milih journal sendiri
    if (temp_journal_detail) {
      const {
        dataSource,
        count,
        total_debit_amount,
        total_credit_amount
      } = this.state;

      const find = dataSource.findIndex(
        currData => currData.id_account === temp_journal_detail.id_account &&
          currData.id_sub_ledger === temp_journal_detail.id_sub_ledger
      );

      const account = accounts.find((data) =>
        data.id_account === temp_journal_detail.id_account &&
        data.id_sub_ledger === temp_journal_detail.id_sub_ledger
      )

      //  baru
      if (find < 0) {
        const init = {
          index_no: count,
          amount: 0,
          debit_amount: 0,
          credit_amount: 0,
          sub_ledger_type: account.sub_ledger_type,
          id_sub_ledger: account.id_sub_ledger ? account.id_sub_ledger : null,
          id_currency: account.id_currency,
          currency_code: account.currency.currency_code,
          currency_rate: getCurrencyRate(account.currency.currency_rate, transaction_date),
          is_base: account.currency.is_base,
          id_location: location.id_location,
          location_name: location.location_name,
          id_department: null,
          id_project: null
        };

        // console.log(getCurrencyRate(account.currency.currency_rate, transaction_date))

        const newData = Object.assign({}, temp_journal_detail, init);
        // console.log(newData);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          total_debit_amount,
          total_credit_amount
        });

        clearJournalDetail(
          [...dataSource, newData],
          total_debit_amount,
          total_credit_amount
        );
        // console.log(this.props.journal);
      }
    }
    // jika temp dari transaksi sebelumnya
    else if (temp_journal_details.length !== 0) {
      let total_debit_amount = 0;
      let total_credit_amount = 0;
      let newIndex = 0;

      temp_journal_details.map(currData => {
        currencies.map((x) => {
          if (x.id_currency === currData.id_currency) {
            currData.currency_rate = getCurrencyRate(x.currency_rate, transaction_date);
            currData.amount = (parseFloat(currData.debit_amount) + parseFloat(currData.credit_amount)) * currData.currency_rate
          }
        })
        total_debit_amount += parseFloat(currData.debit_amount) * parseFloat(currData.currency_rate);
        total_credit_amount += parseFloat(currData.credit_amount) * parseFloat(currData.currency_rate);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...temp_journal_details],
        total_debit_amount,
        total_credit_amount,
        count: newIndex
      });
      clearJournalDetails(temp_journal_details, total_debit_amount, total_debit_amount);
    }
  }

  componentDidUpdate(prevProps) {
    const {
      temp_journal_detail,
      temp_journal_details,
      clearJournalDetail,
      clearJournalDetails,
      clearJournalDetailsState,
      id_location,
      locations,
      accounts,
      transaction_date,
      currencies,
      status,
      check
    } = this.props;

    const [location] = locations.filter(
      currData => currData.id_location === id_location
    );

    // milih journal sendiri
    if (temp_journal_detail) {
      const {
        dataSource,
        count,
        total_debit_amount,
        total_credit_amount
      } = this.state;

      const find = dataSource.findIndex(
        currData => currData.id_account === temp_journal_detail.id_account &&
          currData.id_sub_ledger === temp_journal_detail.id_sub_ledger
      );

      const account = accounts.find((data) =>
        data.id_account === temp_journal_detail.id_account &&
        data.id_sub_ledger === temp_journal_detail.id_sub_ledger
      )

      // console.log(account)
      // journal baru
      if (find < 0) {
        const init = {
          index_no: count,
          amount: 0,
          debit_amount: 0,
          credit_amount: 0,
          sub_ledger_type: account.sub_ledger_type,
          sub_ledger_name: account.sub_ledger_name ? account.sub_ledger_name : null,
          id_sub_ledger: account.id_sub_ledger ? account.id_sub_ledger : null,
          id_currency: account.id_currency,
          currency_code: account.currency.currency_code,
          currency_rate: getCurrencyRate(account.currency.currency_rate, transaction_date),
          is_base: account.currency.is_base,
          id_location: location.id_location,
          location_name: location.location_name,
          id_department: null,
          id_project: null
        };
        const newData = Object.assign({}, temp_journal_detail, init);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          total_debit_amount,
          total_credit_amount
        });

        // show handle Detail untuk pilih sub_ledger_name
        if (account.sub_ledger_type === 1 || account.sub_ledger_type === 2) {
          this.handleDetailModal(newData);
        }

        clearJournalDetail(
          [...dataSource, newData],
          total_debit_amount,
          total_credit_amount
        );
        // console.log(this.props.journal);
      }
    }
    // jika journal dari transaksi sebelumnya
    else if (temp_journal_details.length !== 0) {
      let total_debit_amount = 0;
      let total_credit_amount = 0;
      let newIndex = 0;

      temp_journal_details.map(currData => {

        if (status === 1 && check === -1) {
          currencies.map((x) => {
            if (x.id_currency === currData.id_currency) {
              currData.currency_rate = getCurrencyRate(x.currency_rate, transaction_date);
              currData.amount = (parseFloat(currData.debit_amount) + parseFloat(currData.credit_amount)) * currData.currency_rate
              // console.log("Masuk", currData.currency_rate)
            }
          })
        }

        total_debit_amount += parseFloat(currData.debit_amount) * parseFloat(currData.currency_rate);
        total_credit_amount += parseFloat(currData.credit_amount) * parseFloat(currData.currency_rate);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...temp_journal_details],
        total_debit_amount,
        total_credit_amount,
        count: newIndex
      });
      clearJournalDetails(temp_journal_details, total_debit_amount, total_credit_amount);
    }
    // else if(prevProps.currency_rate !== this.props.currency_rate ){
    //   this.props.journal_details.map(())
    // }
    else if (clearJournalDetailsState) {
      let total_debit_amount = 0;
      let total_credit_amount = 0;
      let newIndex = 0;

      this.setState({
        dataSource: [],
        total_debit_amount,
        total_credit_amount,
        count: newIndex
      });
      clearJournalDetails([], total_debit_amount, total_debit_amount);
    }
  }

  handleDetailModal = row => {
    const { visible } = this.state;
    // console.log(row);

    visible
      ? this.setState({ visible: false, selectedItem: {} })
      : this.setState({ visible: true, selectedItem: row });
  };

  handleDetail = value => {
    this.setState({ visible: false, selectedItem: {} });
    this.handleSave(value);
  };

  handleDelete = index_no => {
    const { handleSave } = this.props;
    let total_debit_amount = 0;
    let total_credit_amount = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      total_debit_amount += parseFloat(currData.debit_amount) * parseFloat(currData.currency_rate);
      total_credit_amount += parseFloat(currData.credit_amount) * parseFloat(currData.currency_rate);
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      total_debit_amount,
      total_credit_amount
    });
    handleSave(newData, total_debit_amount, total_credit_amount);
  };

  handleSave = row => {
    //const { currency_rate } = this.props;
    const newData = this.state.dataSource;
    //console.log(newData);
    //const rate = newData.currency_rate ? parseFloat(newData.currency_rate) : 1;
    let total_debit_amount = 0;
    let total_credit_amount = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    //console.log(index);

    row.amount = (parseFloat(row.debit_amount) + parseFloat(row.credit_amount)) * parseFloat(row.currency_rate);

    // console.log(newData);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    // console.log(newData)

    newData.map(currData => {
      total_debit_amount += parseFloat(currData.debit_amount) * parseFloat(currData.currency_rate);
      total_credit_amount += parseFloat(currData.credit_amount) * parseFloat(currData.currency_rate);
    });


    this.setState({
      dataSource: [...newData],
      total_debit_amount,
      total_credit_amount
    });
    this.props.handleSave(newData, total_debit_amount, total_credit_amount);
  };

  render() {
    // console.log(this.state.dataSource);

    const { dataSource, visible, selectedItem } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const total = getTotal(dataSource);
    const info = [
      {
        index_no: "Total " + getLanguage({id:"debit"}),
        credit_amount: "Total " + getLanguage({id:"debit"}),
        amount: <NumberFormat value={parseFloat(total.total_debit_amount)} />
      },
      {
        index_no: "Total " + getLanguage({id:"credit"}),
        credit_amount: "Total " + getLanguage({id:"credit"}),
        amount: <NumberFormat value={parseFloat(total.total_credit_amount)} />
      }
    ];
    const modifiedData = dataSource.length > 0 ? [...dataSource, ...info] : [];
    // console.log(modifiedData, total);
    let columns = [
      {
        title: getLanguage({ id: "no."}),
        dataIndex: "index_no",
        width: "5%",
        align: "right",
        render: (text, row) => {
          if (isNaN(text)) {
            return {
              //children: text,
              props: {
                colSpan: 4
              }
            };
          } else {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          }
        }
      },
      {
        title: getLanguage({ id: "accountCode"}),
        dataIndex: "account_code",
        width: "25%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "accountName"}),
        dataIndex: "account_name",
        width: "30%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "currency"}),
        dataIndex: "currency_code",
        width: "10%",
        align: "center",
        //editable: true,
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "rate"}),
        dataIndex: "currency_rate",
        width: "15%",
        align: "right",
        onCell: record => {
          if (isNaN(record.index_no)) {
            return {
              colSpan: 0
            };
          }
          else {
            return {
              record,
              editable: true,
              dataIndex: "currency_rate",
              title: "Currency Rate",
              width: "15%",
              handleSave: this.handleSave,
              status: this.props.status
            };
          }
          // else {
          //   return parseFloat(text)
          //   .toFixed(2)
          //   .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
          // }
        }
      },
      {
        title: getLanguage({ id: "debitAmount"}),
        dataIndex: "debit_amount",
        width: "15%",
        align: "right",
        editable: true,
        onCell: record => {
          if (isNaN(record.index_no)) {
            return {
              colSpan: 0
            };
          } else {
            return {
              record,
              editable: true,
              dataIndex: "debit_amount",
              title: "Debit Amount",
              width: "15%",
              handleSave: this.handleSave,
              status: this.props.status
            };
          }
        }
      },
      {
        title: getLanguage({ id: "creditAmount"}),
        dataIndex: "credit_amount",
        width: "15%",
        align: "right",
        editable: true,
        onCell: record => {
          if (isNaN(record.credit_amount)) {
            return {
              children: record.credit_amount,
              props: {
                colSpan: 1
              }
            };
          } else {
            return {
              record,
              editable: true,
              dataIndex: "credit_amount",
              title: "Credit Amount",
              width: "15%",
              handleSave: this.handleSave,
              status: this.props.status
            };
          }
        }
      },
      {
        title: `${getLanguage({ id: "amount"})} (IDR)`,
        width: "15%",
        dataIndex: "amount",
        align: "right",
        render: (text, row) => {
          const val = <NumberFormat value={parseFloat(text)} />
          if (isNaN(row.index_no)) {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          } else {
            return val;
          }
        }
      },
      // {
      //   title: "Location",
      //   dataIndex: "location_name",
      //   width: "20%",
      //   //align: "center",
      //   editable: true,
      //   onCell: record => {
      //     if (isNaN(record.index_no)) {
      //       return {
      //           colSpan: 0
      //       };
      //     } else {
      //       return {
      //         record,
      //         editable: true,
      //         cash_flow_types: this.props.cash_flow_types,
      //         locations: this.props.locations,
      //         dataIndex: "location_name",
      //         title: "Location",
      //         width: "20%",
      //         handleSave: this.handleSave,
      //         status: this.props.status
      //       };
      //     }
      //   }
      // },
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 1
                // fixed: null
              }
            };
          }
          if (!this.props.status || this.props.status === 1) {
            return (
              <div>
                <Tooltip
                  placement="top"
                  title={<IntlMessages id="detail" />}
                >
                  <Button
                    //style={{ borderRadius: "50%" }}
                    className="invoiceEdtBtn"
                    size="small"
                    onClick={() => this.handleDetailModal(row)}
                  >
                    <i className="ion-navicon-round" />
                  </Button>
                </Tooltip>
                <Popconfirm
                  title={<IntlMessages id="confirmDelete" />}
                  onConfirm={() => this.handleDelete(row.index_no)}
                  okText={getLanguage({ id: "yes" })}
                  cancelText={getLanguage({ id: "no" })}
                >
                  {/* <Button style={{ borderRadius: "50%" }} icon="delete" /> */}
                  <Tooltip
                    title={<IntlMessages id="delete" />}
                  >
                    <Button
                      //style={{ borderRadius: "50%" }}
                      className="invoiceDltBtn"
                      size="small"
                    >
                      <i className="ion-trash-b" />
                    </Button>
                  </Tooltip>
                </Popconfirm>
              </div>
            );
          } else {
            return (
              <Tooltip
                placement="top"
                title={<IntlMessages id="detail" />}
              >
                <Button
                  className="invoiceEdtBtn"
                  size="small"
                  onClick={() => this.handleDetailModal(row)}
                >
                  <i className="ion-navicon-round" />
                </Button>
              </Tooltip>
            );
          }
        }
      }
    ];
    
    return (
      <CardWrapper>
        <div>
          <ModalDetail
            visible={visible}
            selectedData={selectedItem}
            status={this.props.status}
            handleDelete={this.handleDelete}
            handleDetailModal={this.handleDetailModal}
            handleDetail={this.handleDetail}
            submit={this.handleDetail}
            departments={this.props.departments}
            projects={this.props.projects}
            customers={this.props.customers}
            suppliers={this.props.suppliers}
            cashbanks={this.props.cashbanks}
          />

          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper
              className="invoiceListTable"
              components={components}
              rowKey={record => record.index_no}
              rowClassName={() => "editable-row"}
              dataSource={modifiedData}
              columns={columns}
              pagination={false}
            // scroll={{ x: 1300 }}
            />
          </Scrollbars>
        </div>
      </CardWrapper>
    );
  }
}

export default EditableTable;
