import React, { Component } from "react";

import moment from "moment";

import EditableTable from "./editableTable";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Col,
  DatePicker,
  Divider,
  Empty,
  Form,
  Icon,
  Input,
  Modal,
  Select,
  Tooltip
} from "antd";
import notification from "../../../../components/notification";
import ModalJournal from "./modalJournal";
import { getCurrencyRate } from "../../../../helpers/helper";

const { Option } = Select;
const InputGroup = Input.Group;
const TextArea = Input.TextArea;

class JournalCreateForm extends Component {
  state = {
    check: -1,
    clearJournalDetailsState: false,
    id_location: this.props.default_employee_location
      ? this.props.default_employee_location.id_location
      : null,
    journal_details: [],
    temp_journal_detail: null,
    temp_journal_details: [],
    total_credit_amount: 0,
    total_debit_amount: 0,
    //currency_rate: null,
    visibleModalJournal: false
  };

  clearJournalDetail = (
    journal_details,
    total_debit_amount,
    total_credit_amount
  ) => {
    this.setState({
      temp_journal_detail: null,
      journal_details: journal_details,
      total_debit_amount,
      total_credit_amount
    });
  };

  clearJournalDetails = (
    journal_details,
    total_debit_amount,
    total_credit_amount
  ) => {
    this.setState({
      clearJournalDetailsState: false,
      temp_journal_details: [],
      journal_details: journal_details,
      total_debit_amount,
      total_credit_amount
    });
  };

  filterJournalDetails = idLocation => {
    const { journal_details } = this.state;
    journal_details.filter(
      data => data.id_location === null || data.id_location !== idLocation
    );
    return journal_details;
  };

  handleModalJournal = () => {
    // console.log("masuk")
    this.setState({
      visibleModalJournal: !this.state.visibleModalJournal
    });
  };

  handleSelectModalJournal = value => {
    const {
      import_journals,
      // accounts,
      locations
    } = this.props.journal;
    const { setFieldsValue, getFieldValue } = this.props.form;

    let [import_journal] = import_journals.filter(
      curr_data => curr_data.id_journal === value
    );

    setFieldsValue({
      id_location: import_journal.id_location
    });

    let journal_details = [];
    let total_debit_amount = 0;
    let total_credit_amount = 0;

    import_journal.journal_details.map((data, key) => {
      // const [account] = accounts.filter((x) =>
      //   data.sub_ledger_type !== 4
      //     ?
      //     x.id_account === data.id_account &&
      //     x.id_sub_ledger === data.id_sub_ledger
      //     :
      //     x.id_account === data.id_account
      // )

      const account = data.account;

      //console.log(accounts, account, data.id_account)

      const [location] = locations.filter(
        x => x.id_location === data.id_location
      );

      let journal_detail = {
        id_account: data.id_account,
        id_sub_ledger: data.sub_ledger_type !== 4 ? data.id_sub_ledger : null,
        sub_ledger_type: data.sub_ledger_type !== 4 ? data.sub_ledger_type : 0,
        account_code: account.account_code,
        account_name: account.account_name,
        debit_amount: parseFloat(data.debit_amount),
        credit_amount: parseFloat(data.credit_amount),
        id_currency: account.id_currency,
        currency_code: account.currency.currency_code,
        //currency_rate: account.currency.currency_rate !== null ? parseFloat(account.currency.currency_rate.rate) : 1,
        currency_rate: getCurrencyRate(
          account.currency.currency_rate,
          getFieldValue("transaction_date")
        ),
        is_base: account.currency.is_base,
        id_location: location.id_location,
        location_name: location.location_name,
        id_department: data.id_department,
        id_project: data.id_project,
        index_no: data.index_no
      };

      //console.log(getCurrencyRate(account.currency.currency_rate, getFieldValue("transaction_date")))

      journal_detail.amount =
        (parseFloat(data.debit_amount) + parseFloat(data.credit_amount)) *
        journal_detail.currency_rate;

      journal_details.push(journal_detail);
      total_debit_amount +=
        parseFloat(data.debit_amount) * parseInt(journal_detail.currency_rate);
      total_credit_amount +=
        parseFloat(data.credit_amount) * parseInt(journal_detail.currency_rate);
    });

    //console.log(journal_details);

    this.setState({
      temp_journal_details: journal_details,
      total_debit_amount,
      total_credit_amount
    });

    this.handleModalJournal();
  };

  handleSave = (journal_details, total_debit_amount, total_credit_amount) => {
    this.setState({
      journal_details: journal_details,
      total_debit_amount,
      total_credit_amount
    });
  };

  handleChangeTransDate = () => {
    this.setState({
      temp_journal_details: this.state.journal_details,
      journal_details: this.state.temp_journal_details
    });
  };

  handleChangeGLAccount = e => {
    //console.log(e)
    const { accounts, suppliers } = this.props.journal;
    const [account] = accounts.filter(
      data => data.account_code + " " + data.account_name === e
    );

    // let exist = true //indikator apakah punya sub ledger dengan currency yang sama
    let haveSup = true; //inkator apakah punya subledger supplier

    if (account.sub_ledger_type === 1) {
      // cek apakah punya list supplier
      const findSup = suppliers.find(
        data => data.id_location === account.id_location
      );
      if (findSup) {
        // do nothing
      } else {
        haveSup = false;
      }
    }

    if (!haveSup) {
      Modal.error({
        title: "Error",
        content: "This location doesn't have any suppliers yet"
      });
    } else {
      this.setState({
        temp_journal_detail: account
      });
    }
  };

  handleChangeLocation = value => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({
      id_account: [],
      id_cash_bank: null
    });
    this.setState({
      id_location: value,
      clearJournalDetailsState: true
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      // const { getFieldValue } = this.props.form;
      const { messages } = this.props.journal;
      // console.log(getFieldValue('currency_amount'))
      // console.log(this.state.total_debit_amount);

      if (this.state.journal_details.length < 1) {
        notification("error", messages.gl_account);
      } else if (
        this.state.total_debit_amount === 0 ||
        this.state.total_credit_amount === 0
      ) {
        notification("error", messages.zero_amount);
      } else if (
        this.state.total_debit_amount !== this.state.total_credit_amount
      ) {
        notification("error", messages.unbalance_amount);
      } else if (!err) {
        values.transaction_date = values.transaction_date.format(
          "YYYY-MM-DD HH:mm:ss"
        );
        values.journal_details = [];
        this.state.journal_details.map(detail => {
          if (
            parseFloat(detail.debit_amount) === 0 &&
            parseFloat(detail.credit_amount) === 0
          ) {
            // Ha Kosong ... 我的魔法 哈哈哈
          } else {
            values.journal_details.push(detail);
          }
          if (!detail.sub_ledger_name) {
            detail.sub_ledger_name = null;
          }
        });
        // console.log("submit", values)
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.state);
    // console.log(this.props.journal)
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { journal, default_employee_location } = this.props;
    const { loading, loadingImport } = journal;

    let permissions = this.props.permissions;

    const error = journal.errorField ? journal.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <ModalJournal
          visible={this.state.visibleModalJournal}
          journal={journal}
          id_location={getFieldValue("id_location")}
          handleModal={this.handleModalJournal}
          handleSelectModal={this.handleSelectModalJournal}
        />

        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div
            style={{ padding: "0 15px 0 0" }}
            className="isoColumn-2 isoFormGroupComp"
          >
            <Form.Item
              {...formItemLayout}
              label={
                <Tooltip
                  placement="top"
                  title={<IntlMessages id="journals.copy.help" />}
                >
                  <IntlMessages id="journals.copy" />
                  <Icon
                    style={{ marginLeft: "5px" }}
                    type="question-circle"
                    theme="filled"
                  />
                </Tooltip>
              }
            >
              <Button
                icon="select"
                loading={loadingImport}
                disabled={loadingImport || !getFieldValue("id_location")}
                onClick={this.handleModalJournal}
                style={{ height: "31px", width: "100%" }}
                // shape="circle"
              >
                <IntlMessages id="button.copy" />
              </Button>
            </Form.Item>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="purchaseOrders.transactionDate" />}
                >
                  {getFieldDecorator("transaction_date", {
                    initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY HH:mm:ss"
                      disabled={
                        !permissions.includes("Can Back Date") ? true : false
                      }
                      onChange={this.handleChangeTransDate}
                      onOpenChange={this.handleChangeTransDate}
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment()}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    this.state.id_location &&
                    this.state.journal_details.length > 0 ? (
                      <Tooltip
                        placement="top"
                        title={<IntlMessages id="journals.location.help" />}
                      >
                        <IntlMessages id="locations.pageHeader" />
                        <Icon
                          style={{ marginLeft: "5px" }}
                          type="question-circle"
                          theme="filled"
                        />
                      </Tooltip>
                    ) : (
                      <IntlMessages id="locations.pageHeader" />
                    )
                  }
                >
                  {getFieldDecorator("id_location", {
                    initialValue: default_employee_location.id_location,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.journal.loading}
                      showSearch
                      optionFilterProp="children"
                      onChange={this.handleChangeLocation}
                      style={{ width: "100%" }}

                      // disabled={used ? true : false}
                    >
                      {journal.locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </div>
          <div
            style={{ padding: "0 0 0 15px" }}
            className="isoColumn-2 isoFormGroupComp"
          >
            <Form.Item {...formItemLayout} label={<IntlMessages id="note" />}>
              {getFieldDecorator("note", {
                //initialValue: data.description,
              })(<TextArea autosize={{ minRows: 4 }} />)}
            </Form.Item>
          </div>
        </div>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="journals.add.subtitle.GLAccount" />
        </Divider>

        <Form.Item
          {...formItemLayout}
          //label={<IntlMessages id="GLAccount" />}
        >
          {getFieldDecorator(
            "id_account",
            {}
          )(
            <Select
              loading={this.props.journal.loading}
              showSearch
              optionFilterProp="children"
              onChange={this.handleChangeGLAccount.bind(this)}
              // onOpenChange={this.handleChangeGLAccount}
              // disabled={used ? true : false}
              notFoundContent={
                getFieldValue("id_location") ? (
                  <Empty description={<IntlMessages id="noData" />} />
                ) : (
                  <Empty description={<IntlMessages id="noLocation" />} />
                )
              }
            >
              {getFieldValue("id_location")
                ? journal.accounts.map((data, key) => {
                    if (
                      data.id_location === getFieldValue("id_location") ||
                      !data.id_location
                    ) {
                      return (
                        <Option
                          key={key}
                          value={data.account_code + " " + data.account_name}
                          // disabled={
                          //   data.sub_ledger_type === 0
                          //     ? false
                          //     : true
                          // }
                        >
                          {data.account_code + " " + data.account_name}
                        </Option>
                      );
                    }
                  })
                : null}
            </Select>
          )}
        </Form.Item>

        <EditableTable
          accounts={this.props.journal.accounts}
          check={this.state.check}
          clearJournalDetailsState={this.state.clearJournalDetailsState}
          clearJournalDetail={this.clearJournalDetail}
          clearJournalDetails={this.clearJournalDetails}
          cashbanks={this.props.journal.cashbanks}
          currencies={this.props.journal.currencies}
          customers={this.props.journal.customers}
          departments={this.props.journal.departments}
          handleSave={this.handleSave}
          journal_details={this.state.journal_details}
          id_location={getFieldValue("id_location")}
          locations={this.props.journal.locations}
          projects={this.props.journal.projects}
          status={1}
          suppliers={this.props.journal.suppliers}
          temp_journal_detail={this.state.temp_journal_detail}
          temp_journal_details={this.state.temp_journal_details}
          transaction_date={getFieldValue("transaction_date")}
        />

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedJournalCreateForm = Form.create({ name: "create_journal" })(
  JournalCreateForm
);

export default WrappedJournalCreateForm;
