import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/generalLedger/journal/actions";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import NumberFormat from "../../../App/NumberFormat";

import {
  Modal,
  Tooltip,
  Table,
  Collapse,
  Select,
  DatePicker,
  Input,
  Row,
  Col,
  Empty
} from "antd";
import {
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;
const Search = Input.Search;
const { getImportJournals } = actions;

const expandedRowRender = data => {
  const columns = [
    {
      title: getLanguage({ id: "no."}),
      dataIndex: "index_no",
      width: "5%"
    },
    {
      title: getLanguage({ id: "accountCode"}),
      dataIndex: "account.account_code",
      width: "10%",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "accountName"}),
      dataIndex: "account.account_name",
      width: "15%",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "currency"}),
      dataIndex: "currency.currency_code",
      width: "5%",
      align: "center",
      render: text => {
        if (text && text.length > 25) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 25) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: getLanguage({ id: "rate"}),
      dataIndex: "currency_rate",
      width: "10%",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
    {
      title: getLanguage({ id: "debitAmount"}),
      dataIndex: "debit_amount",
      width: "15%",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
    {
      title: getLanguage({ id: "creditAmount"}),
      dataIndex: "credit_amount",
      width: "15%",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
    {
      title: `${getLanguage({ id: "amount"})} (IDR)`,
      width: "15%",
      dataIndex: "amount",
      align: "right",
      render: text => <NumberFormat value={text} />
    },
    // {
    //   title: "Location",
    //   dataIndex: "location_name",
    //   width: "20%",
    //   //align: "center",
    //   editable: true,
    //   render: text => {
    //     if (text && text.length > 25) {
    //       return (
    //         <Tooltip placement="bottom" title={text}>
    //           {text.substring(0, 25) + "..."}
    //         </Tooltip>
    //       );
    //     } else {
    //       return text;
    //     }
    //   }
    // },
  ];

  const dataSource = data;
  return (
    <div>
      <strong>
        <IntlMessages id="gLAccountDetails" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={true}
        style={{ marginTop: "24px" }}
      />
    </div>
  );
};

class ModalJournal extends Component {
  state = {
    filter: {
      id_location: null,
      status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  handleSelect = value => {
    const { handleSelectModal } = this.props;
    handleSelectModal(value);
  };

  handleLocationChange = id_location => {
    const { getImportJournals } = this.props;
    const string = "id_location=" + id_location;
    getImportJournals(1, null, string);
  };

  componentDidUpdate(prevProps) {
    const { id_location } = this.props;

    if (prevProps.id_location !== id_location) {
      this.handleLocationChange(id_location);
      this.setState({
        filter: {
          ...this.state.filter,
          id_location: id_location
        }
      });
    }
  }

  // Handle search
  handleSearch = value => {
    const { getImportJournals } = this.props;
    this.setState({
      filter: {
        id_location: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    getImportJournals(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getImportJournals } = this.props;
    this.setState({
      filter: {
        id_location: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getImportJournals();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    const { getImportJournals } = this.props;
    //const { current_page } = this.props.journal;
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }

    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getImportJournals(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getImportJournals } = this.props;
    this.setState({
      filter: {
        id_location: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getImportJournals();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateJournal(id);
    } else {
      this.props.deactivateJournal(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getImportJournals } = this.props;
    const { import_search, import_filter } = this.props.journal;
    getImportJournals(page, import_search, import_filter);
  };


  render() {
    // console.log(this.props);

    const { visible, handleModal, journal, id_location } = this.props;
    const { import_journals, locations, cash_banks } = journal ? journal : [];
    const { loadingImport } = journal ? journal : false;
    const dataSource = [];
    Object.keys(import_journals).map((journal, index) => {
      return dataSource.push({
        ...import_journals[journal],
        key: journal
      });
    });

    const {
      import_current_page,
      import_page_size,
      import_total
    } = this.props.journal;

    let columns = [
      {
        title: getLanguage({ id: "journals.no"}),
        dataIndex: "transaction_no",
        rowKey: "transaction_no",
        width: "25%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "ordered";
              break;
            // case 2:
            //   className = "closed";
            //   break;
            case 3:
              className = "cancelled";
              break;
            case 4:
              className = "closed";
              break;
            default:
              className = "new";
              break;
          }
  
          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <Tooltip
                placement="bottom"
                title={<IntlMessages id="tooltip.select" />}
              >
                <a
                  onClick={e => {
                    e.preventDefault();
                    this.handleSelect(row.id_journal);
                  }}
                  href="javascript:;"
                >
                  {text ? <TransNo>{text}</TransNo> : <TransNo>Open</TransNo>}
                </a>
              </Tooltip>
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "date"}),
        dataIndex: "transaction_date",
        dataIndex: "transaction_date",
        rowKey: "expected_date",
        width: "15%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "sidebar.location"}),
        dataIndex: "location.location_name",
        rowKey: "location.location_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "totalDebit"}),
        dataIndex: "total_debit_amount",
        rowKey: "total_debit_amount",
        width: "10%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "totalCredit"}),
        dataIndex: "total_credit_amount",
        rowKey: "total_credit_amount",
        width: "10%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "createdBy"}),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status"}),
        dataIndex: "status",
        rowKey: "status",
        width: "15%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({ id: "open"});
              break;
            case 2:
              className = "ordered";
              display = getLanguage({ id: "processed"});
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "cancelled"});
              break;
            case 4:
              className = "closed";
              display = getLanguage({ id: "closed"});
              break;
            default:
              className = "new";
              display = getLanguage({ id: "open"});
              break;
          }
  
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    return (
      <Modal
        title={<IntlMessages id="journals.copy" />}
        visible={visible}
        centered
        footer={null}
        keyboard={true}
        onCancel={handleModal}
        width="95%"
      >
        <Row style={{ marginBottom: "10px" }} type="flex">
          <Col span={18}>
            <Search
              placeholder={
                getLanguage({id: "journals.no"}) + ", " +
                getLanguage({id: "createdBy"})
              }
              onSearch={this.handleSearch}
              style={{ width: "99%", height: "36px" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
          </Col>
          <Col span={6}>
            <Button
              type="primary"
              onClick={this.handleSearch.bind(this, this.state.search)}
              style={{ marginRight: "2%" }}
            >
              <IntlMessages id="button.search" />
            </Button>

            <Button onClick={this.handleResetSearch}>
              <IntlMessages id="filter.reset" />
            </Button>
          </Col>
        </Row>
        <Row style={{ marginBottom: "10px" }}>
          <Col>
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="journals.filter.select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingImport}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="journals.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={true}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                </Row>
                <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={4}>
                    <label>Status</label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(this, "status")}
                      value={
                        this.state.filter.status ? this.state.filter.status : []
                      }
                      style={{ width: "90%" }}
                      placeholder="Status"
                      optionFilterProp="children"
                      filterOption={true}
                      // loading={this.props.location.loadingFilterSites}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {/* <Option value={1}>Open</Option> */}
                      <Option value={2}> {getLanguage({ id: "processed"})} </Option>
                      <Option value={4}> {getLanguage({ id: "closed"})} </Option>
                      <Option value={3}> {getLanguage({ id: "cancelled"})} </Option>
                    </Select>
                  </Col>

                  {/* </Row>
                <Row type="flex" justify="start"> */}
                  <Col span={4}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : import_page_size
                      }
                      style={{ width: "80%" }}
                      // placeholder={
                      //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                      // }
                      optionFilterProp="children"
                      filterOption={true}
                      //allowClear={true}
                      //showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </Col>
                  <Col
                    span={4}
                    style={{
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </Col>
        </Row>

        <Row>
          <Col>
            {dataSource.length === 0 && !loadingImport ? (
              <Empty
                description={<IntlMessages id="journals.table.noData" />}
              />
            ) : (
              <Table
                dataSource={dataSource}
                columns={columns}
                loading={loadingImport}
                expandedRowRender={record =>
                  expandedRowRender(record.journal_details)
                }
                expandIcon={props => (
                  <Button
                    onClick={e => props.onExpand(props.record, e)}
                    size="small"
                  >
                    <IntlMessages id="button.view" />
                  </Button>
                )}
                pagination={
                  import_page_size
                    ? {
                        current: import_current_page,
                        pageSize: parseInt(import_page_size),
                        total: import_total,
                        onChange: this.handlePageChange.bind(this)
                      }
                    : true
                }
              />
            )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(
  connect(
    null,
    { getImportJournals }
  )
)(ModalJournal);
